<template>
    <div class="update">
        <!-- 头部 -->
        <div style="width:1202px;margin:20px auto 0;text-align:center;height:60px;line-height:60px;background-color:#545c64;border-radius:10px 10px 0 0 ;color:white">设备信息
        </div>
        <!-- 修改内容 -->
        <div style="width:1200px;margin:auto;display:flex;border:1px solid">
            <!-- 属性名-->
            <div style="width:33.3%;border-right:1px solid">
                <ul>
                    <li style="height:50px;text-align:center;line-height:50px;font-size:20px;background-color:#909399">属性名</li>
                    <li style="border-bottom:1px solid #545c64" v-for="(item,index) in shuxing" :key="index">
                        <div style="height:50px;text-align:center;line-height:50px;font-size:20px">{{item}}</div>
                    </li>
                </ul>
            </div>
            <!-- 属性值 -->
            <div style="width:33.3%;border-right:1px solid">
                <ul>
                    <li style="height:50px;text-align:center;line-height:50px;font-size:20px;background-color:#909399">属性值</li>
                    <li v-for="(item,index) in value" :key="index" style="border-bottom:1px solid #545c64" >
                        <div style="height:50px;text-align:center;line-height:50px;font-size:20px">{{item}}</div>
                    </li>
                </ul>
            </div>
            <!-- 操作 -->
            <div style="width:33.4%;">
                <ul>
                    <li style="height:50px;text-align:center;line-height:50px;font-size:20px;background-color:#909399">操作</li>
                    <li v-for="(item,index) in value" :key="index" style="border-bottom:1px solid #545c64;text-align:center;line-height:50px">
                        <el-button type="primary" @click="update(index)">修改</el-button>
                        <el-button type="danger" @click="delate(index)">删除</el-button>
                    </li>
                </ul>
            </div>
        </div>
        <!-- 配置信息 -->
        <div class="emassege" style="width:1202px;text-align:center;margin:auto;background-color:#545c64;height:50px;line-height:50px;color:white">
            自定义配置信息
        </div>

        <div style="width:1200px;margin:auto;display:flex;border:1px solid">

            <!-- 属性名-->
            <div style="width:33.3%;border-right:1px solid">
                <ul>
                    <li style="height:50px;text-align:center;line-height:50px;font-size:20px;background-color:#909399">属性名</li>
                    <li style="border-bottom:1px solid #545c64;height:50px;line-height:50px;text-align:center" v-for="(item,index) in messageArr" :key="index">{{item.key}}</li>
                </ul>
            </div>
            <!-- 属性值 -->
            <div style="width:33.3%;border-right:1px solid">
                <ul>
                    <li style="height:50px;text-align:center;line-height:50px;font-size:20px;background-color:#909399">属性值</li>
                    <li style="border-bottom:1px solid #545c64;height:50px;line-height:50px;text-align:center" v-for="(item,index) in messageArr" :key="index">{{item.value}}</li>
                </ul>
            </div>
            <!-- 操作 -->
            <div style="width:33.4%;">
                    <ul>
                        <li style="height:50px;text-align:center;line-height:50px;font-size:20px;background-color:#909399">操作</li>
                        <li v-for="(item,index) in messageArr" :key="index" style="border-bottom:1px solid #545c64;text-align:center;line-height:50px">
                            <el-button type="primary" @click="updateMessage(index)">修改</el-button>
                            <el-button type="danger" @click="delateMessage(index)">删除</el-button>
                        </li>
                    </ul>
            </div>

        </div>

        <!-- 增加配置项 -->
        <div style="width:1202px;text-align:center;margin:auto"><el-button type="primary" style="width:100%" @click="addMessageshow">增加配置信息</el-button></div>
        <!-- 服务管理 -->
        <div style="width:1202px;margin:20px auto 0;font-size:20px;color:#409EFF;cursor:pointer" @click="serverService">服务管理>>></div>
        <!-- 增加信息会话框 -->
        <el-dialog
        title="增加配置信息"
        :visible.sync="dialogVisible"
        width="30%"
        >
        <span>属性名</span>
        <el-input v-model="addData.key" placeholder="请输入内容"></el-input>
        <span>属性值</span>
        <el-input v-model="addData.value" placeholder="请输入内容"></el-input>


        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addMessage">确 定</el-button>
        </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name:'Update',
    data(){
        return{
            //提交的数据
            obj:{},
            //属性值
            value:[],
            //属性名
            keys:[],
            //控制添加信息会话框显示
            dialogVisible:false,
            //新增配置项信息
            addData:{
                key:'',
                value:''
            },
            //更多配置信息
            eMessage:[],
            messageArr:[],
            // 属性名数组
            shuxing:['设备外网IP','设备内网IP','设备开放端口','设备域名','设备域名负责人id','负责人名字'],
            //设备Id
            eId:''
        }
    },
    methods:{
        //修改
        update(index){
            this.$prompt('修改设备信息', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({ value }) => {
                this.$forceUpdate();
                this.value[index] = value
                this.obj[this.keys[index]] = value
                let obj = JSON.parse(localStorage.getItem('data'))
                obj[this.keys[index]] = value
                localStorage.setItem('data',JSON.stringify(obj))
                this.$axios.put('http://192.168.1.155:8888/equipmentController',this.obj).then((msg)=>{
                    if (msg.data) {
                        this.$message({
                            message: '恭喜你，修改成功',
                            type: 'success'
                        });
                    }else{
                        this.$message.error('修改失败');
                    }
                })
            });
        },
        //删除
        delate(index){
            delete this.obj[this.keys[index]]
            let obj = JSON.parse(localStorage.getItem('data'))
            delete obj[this.keys[index]]
            this.shuxing.splice(index,1)
            this.keys.splice(index,1)
            this.value.splice(index,1)
            localStorage.setItem('data',JSON.stringify(obj))
            this.$axios.put('http://192.168.1.155:8888/equipmentController',this.obj).then((msg)=>{
                    if (msg.data) {
                    this.$message({
                        message: '恭喜你，删除成功',
                        type: 'success'
                    });
                    }else{
                        this.$message.error('删除失败');
                    }

                })
        },
        //显示增加信息会话框
        addMessageshow(){
            this.dialogVisible = true
        },
        //提交增加配置项
        addMessage(){
            //属性名不能为空
            if (!this.addData.key) {
                this.$message({
                    message: '属性名不能为空',
                    type: 'warning'
                });
            }
            //属性值不能为空
            if (!this.addData.value) {
                this.$message({
                    message: '属性值不能为空',
                    type: 'warning'
                });
            }
            //提交
            if (this.addData.key&&this.addData.value) {
                let obj = {
                    'key':this.addData.key,
                    'value':this.addData.value
                }
                this.messageArr.push(obj)
                this.obj.eMessage = JSON.stringify(this.messageArr)
                this.$axios.put('http://192.168.1.155:8888/equipmentController',this.obj).then((msg)=>{
                    
                   if (msg.data) {
                    this.$message({
                        message: '恭喜你，添加成功',
                        type: 'success'
                    });
                    this.dialogVisible = false
                    }else{
                        this.$message.error('添加失败');
                    }
                })
            }
        },
        //服务管理
        serverService(){
            this.$router.push({
                name:'ServerService'
            }).then(()=>{
                localStorage.setItem('eId',this.eId)
            })
        },
        //修改自定义信息
        updateMessage(index){
            this.$prompt('修改设备信息', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({ value }) => {
                this.messageArr[index].value = value
                this.obj.eMessage = JSON.stringify(this.messageArr)
                this.$axios.put('http://192.168.1.155:8888/equipmentController',this.obj).then((msg)=>{
                    if (msg.data) {
                    this.$message({
                        message: '恭喜你，修改成功',
                        type: 'success'
                    });
                    }else{
                        this.$message.error('修改失败');
                    }
                })
            });
        },
        //删除自定义信息
        delateMessage(index){
            this.messageArr.splice(index,1)
            this.obj.eMessage = JSON.stringify(this.messageArr)
            this.$axios.put('http://192.168.1.155:8888/equipmentController',this.obj).then((msg)=>{
                    if (msg.data) {
                    this.$message({
                        message: '恭喜你，删除成功',
                        type: 'success'
                    });
                    }else{
                        this.$message.error('删除失败');
                    }
                })
        }
    },
    mounted(){
        let obj = JSON.parse(localStorage.getItem('data'))
        this.eId = obj.eId
        this.obj = obj
        this.messageArr = JSON.parse(this.obj.eMessage) || []
        let arr = Object.keys(obj)
        for (let i = 0; i < arr.length; i++) {
            if (arr[i] !== 'eId' && arr[i] !=='eName' && arr[i]!=='eMessage') {
                this.keys.push(arr[i])
            }
        }
        for (let i in obj) {
            if (i !== 'eId' && i !== 'eName' && i !== 'eMessage') {
                this.value.push(obj[i])
            }
        }
    }
}
</script>

<style scoped>
ul li{
    list-style: none;
}
</style>


